.statList {

    display: grid;
    grid-template-columns: repeat(auto-fit,minmax(70px, auto));
    justify-content: center;
    margin-top: 30px;
    padding:0;
}
.title {
    text-align: center;
}

.statSection {
    display: block;
    margin-left: auto;
    margin-right: auto;
    margin-top: 30px;
    border-radius: 3px;
    background-color: rgb(168, 165, 165);
    width: 400px;
    border: 1px solid black;
}