.statSpan {
  text-align: center;
}
.staticItem {
    padding:5px;
    display: grid;
    grid-template-columns: repeat(auto-fit,minmax(70px, auto));
    width:75px;
    height:75px;
    list-style: none;
    color: grey;
    font-weight: bold;
    background-color: rgb(231, 200, 223);
    border: 1px solid black;
    border-radius: 5px;
   
}
