.friendList {
   
    padding:0;
}
.avatar {
    width:50px;
}
.name {
    font-size:18px;
    margin-top : 5px;
    margin-bottom: 0;
    text-align: center;
}
.friendItem {
    list-style: none;
    border: 1px solid black;
    border-radius: 5px;
    padding: 8px 20px;
    margin-right: 5px;
}
.status {
    display: block;
    margin-top:8px;
    margin-left: auto;
    margin-right: auto;
    width: 15px;
   
    border-radius: 80px;
  
}
.true {
color: green;
}
.false {
color: red;
}